.buttonItem {
  width: auto;
  text-align: center;
  button {
    margin: 10px;
    padding: 5px 20px;
    border: none;
    outline: none;
    border-radius: 3px;
    background: #06A4FF;
    color: #ffffff;
    cursor: pointer;
    transition: all .2s;
    &:hover {
      background: darken(#06A4FF, 10%); } }

  button:disabled {
    background: #969696; } }

.checkboxItem {
  color: #969696; }



//Errors Style
.formCommonError {
  color: #c90813;
  padding: 0 0 20px 0;
  text-align: center; }

.error {
  color: #c90813; }
