.inputItem {
  height: 15px;
  text-align: center;

  input {
    border: none;
    outline: none;
    font-size: 16px; } }

.spanItem {
  height: 15px;
  text-align: center;
  p {
    color: #06A4FF;
    font-size: 14px;
    margin: 0;
    cursor: pointer;
    transition: all .2s; }

  & p:hover {
    color: #1fdaff; } }
